import { http } from "../utils/request";

// 
export const loginAPI = (data) => {
  return http({
    url: '/sht/user/login',
    method: 'POST',
    data,
  });
};

//
export const registerAPI = (data) => {
  return http({
    url: '/sht/user/register',
    method: 'POST',
    data,
  });
};

// 
export const getUserInfoAPI = () => {
  return http({
    url: '/sht/user/info',
    method: 'GET',
  });
};

// 
export const updateUserInfoAPI = (data) => {
  return http({
    url: '/sht/user/info',
    method: 'POST',
    data
  });
};

