const userModule = {
	namespaced: true, // 
	state: {
		userInfo: null, // 
		token: null,
	},
	mutations: {
		// 
		setUserInfo(state, userInfo) {
			state.userInfo = userInfo;
		},
		// 
		clearUserInfo(state) {
			state.userInfo = null;
		},
		// token
		setToken(state, token) {
			state.token = token;
		},
		// token
		clearToken(state) {
			state.token = null;
		},
	},
	actions: {
		// 
		setUserInfo({
			commit
		}, userInfo) {
			commit('setUserInfo', userInfo);
		},
		// 
		clearUserInfo({
			commit
		}) {
			commit('clearUserInfo');
		},
		// token
		setToken({
			commit
		}, token) {
			commit('setToken', token);
		},
		// token
		clearToken({
			commit
		}) {
			commit('clearToken');
		},
	},
	getters: {
		// 
		userInfo: (state) => state.userInfo,
		token: (state) => state.token,
	},
	plugins: [
		//  uni 
		(store) => {
			store.subscribe((mutation, state) => {
				if (mutation.type === 'setUserInfo') {
					uni.setStorageSync('userInfo', state.userInfo); //  uni  localStorage
				}
				if (mutation.type === 'clearUserInfo') {
					uni.removeStorageSync('userInfo'); //  uni  localStorage 
				}
				if (mutation.type === 'setToken') {
					uni.setStorageSync('token', state.token); //  uni  localStorage
				}
				if (mutation.type === 'clearToken') {
					uni.removeStorageSync('token'); //  uni  localStorage 
				}
			});

			// 
			const userInfo = uni.getStorageSync('userInfo');
			if (userInfo) {
				store.commit('setUserInfo', userInfo);
			}
			const token = uni.getStorageSync('token');
			if (token) {
				store.commit('setToken', token);
			}
		},
	],
};

export default userModule;