import { http } from "../utils/request";

export const uploadAPI = (filePath) => {
  return new Promise((resolve, reject) => {
    uni.uploadFile({
      url: 'http://localhost:8088/sht/user/file', // URL
      filePath: filePath,
      name: 'file', // @RequestParam
      formData: {
        // 
        'userId': '123'
      },
      success: (uploadRes) => {
        if (uploadRes.statusCode === 200) {
          try {
            resolve(JSON.parse(uploadRes.data));
          } catch (e) {
            resolve(uploadRes.data);
          }
        } else {
          reject(new Error(`: ${uploadRes.statusCode}`));
        }
      },
      fail: (err) => {
        reject(err);
      }
    });
  });
};

// chat
export const chatAPI = (data) => {
  return http({
    url: '/chat',
    method: 'POST',
    data,
  });
};
