// utils/request.js
const baseURL = 'http://localhost:8088'; // 
// const baseURL = 'https://api.yourdomain.com'; // 

// store
let userStore = null;

// 
export const initRequestInterceptor = (store) => {
  userStore = store;
  
  const httpInterceptor = {
    invoke(options) {
      // URL
      if (!options.url.startsWith('http')) {
        // URL
        const path = options.url.startsWith('/') 
          ? options.url.slice(1) 
          : options.url;
        options.url = `${baseURL}/${path}`;
      }
      
      options.timeout = 10000;
      
      // 
      options.header = {
        ...options.header,
        'source-client': 'miniapp',
        'Content-Type': 'application/json'
      };

      // tokenstore
      const token =  uni.getStorageSync('token');
	  console.log(token)
      if (token) {
        options.header.Authorization = `${token}`;
      }

      console.log(':', options);
    }
  };

  // 
  uni.addInterceptor('request', httpInterceptor);
  uni.addInterceptor('uploadFile', httpInterceptor);
};

// http
export const http = (options) => {
  return new Promise((resolve, reject) => {
    // URL
    if (!options.url.startsWith('http')) {
      options.url = `${baseURL}/${options.url.replace(/^\//, '')}`;
    }

    uni.request({
      ...options,
      success(res) {
        console.log(':', res);
        if (res.statusCode >= 200 && res.statusCode < 300) {
		if(res.data.message && res.data.message !== 'success'){
			if(res.data.message === ''){
				uni.showToast({
					title: '',
					icon: 'none'
				});
			}else{
				uni.showToast({
					title: res.data.message,
					icon: 'none'
				});
			}			
		}			
          resolve(res.data);
        } else if (res.statusCode === 401) {
          uni.removeStorageSync('token');
          uni.navigateTo({ url: '/pages/login/login' });
          uni.showToast({
          	title: ``,
          	icon: 'none'
          });
        } else {
          const errorMsg = res.data?.message || `: ${res.statusCode}`;
          uni.showToast({
            icon: 'none',
            title: errorMsg,
          });
          reject(new Error(errorMsg));
        }
      },
      fail(err) {
        console.error(':', err);
        uni.showToast({
          icon: 'none',
          title: '',
        });
        reject(err);
      },
    });
  });
};